* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f0f0f0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.form {
  max-width: 400px;
  background-color: #fff;
  border-radius: 5px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.form label {
  margin-bottom: 10px;
  font-weight: bold;
  display: block;
}

.form input[type="number"] {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 3px;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

.form button[type="submit"] {
  width: 100%;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 3px;
  padding: 10px;
  font-size: 18px;
  transition: background-color .3s;
  display: block;
}

.form button[type="submit"]:hover {
  background-color: #0056b3;
}

p {
  text-align: center;
  margin-top: 20px;
}

a {
  color: #007bff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

